import * as Yup from 'yup'
import emailjs from 'emailjs-com'
import React, { useRef } from 'react'
import { Form, Formik } from 'formik'
import FormControl from '../COMPONENTS/FormControl'
import { Button } from '@material-ui/core'

const ContactMe = () => {
	const initialValues = {
		name: '',
		email: '',
		subject: '',
		message: '',
	}

	const form = useRef()
	const deliverMsg = (msg, onSubmitProps) => {
		emailjs
			.sendForm(
				'service_ho4swee',
				'template_j6zokor',
				form.current,
				'user_ZIWXqpNHRWMObiqSZoEEP'
			)
			.then(
				(result) => {
					console.log(result.text)
				},
				(error) => {
					console.log(error.text)
				}
			)
		console.log(msg, form)
		onSubmitProps.resetForm()
	}

	const validationSchema = Yup.object({
		name: Yup.string().required(
			'Enter your Name'
		),
		email: Yup.string()
			.email('Please Enter a valid Email format')
			.required('Enter your Email'),
		message: Yup.string().required(
			'Type in your Message'
		),
	})

	return (
		<div className='ContactMeWrapper'>
			<div className='ContactMe'>
				<h2>get in touch !</h2>
				<Formik
					initialValues={initialValues}
					validationSchema={validationSchema}
					onSubmit={deliverMsg}
				>
					{(formik) => {
						return (
							<Form>
								<FormControl
									err={formik.errors}
									name='name'
									type='text'
									label='Name'
									placeholder='Adeboyejo David'
									autoComplete='off'
									data-fnt={true}
									star={true}
								/>
								<FormControl
									err={formik.errors}
									name='email'
									type='email'
									label='Email'
									autoComplete='off'
									placeholder='official-mail@yahoo.com'
									star={true}
								/>
								<FormControl
									err={formik.errors}
									name='subject'
									type='text'
									label='Subject'
									autoComplete='off'
									placeholder='New E-commerce Site'
								/>
								<FormControl
									err={formik.errors}
									name='message'
									control='textarea'
									label='Message'
									autoComplete='off'
									placeholder="Hey David, I have a project 'X' I would really love to be online. How soon can you get on it ?"
									star={true}
								/>
								<Button
									type='submit'
									className='msgBtn'
								>
									Send
									<svg
										xmlns='http://www.w3.org/2000/svg'
										viewBox='0 0 512 512'
									>
										<path d='M476 3.2L12.5 270.6c-18.1 10.4-15.8 35.6 2.2 43.2L121 358.4l287.3-253.2c5.5-4.9 13.3 2.6 8.6 8.3L176 407v80.5c0 23.6 28.5 32.9 42.5 15.8L282 426l124.6 52.2c14.2 6 30.4-2.9 33-18.2l72-432C515 7.8 493.3-6.8 476 3.2z' />
									</svg>
								</Button>
							</Form>
						)
					}}
				</Formik>
			</div>
			<div className='circleWrapper' />
		</div>
	)
}

export default ContactMe
